import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { EventoShellComponent } from './evento-shell/evento-shell.component';
import { EventoListaComponent } from './evento-lista/evento-lista.component';
import { EventoNuevoComponent } from './evento-nuevo/evento-nuevo.component';
import { EventoEdicionComponent } from './evento-edicion/evento-edicion.component';

const routes: Routes = [
  { path: '', component: EventoShellComponent, children: [
      { path: '', redirectTo: 'lista'},
      { path: 'lista', component: EventoListaComponent},
      { path: 'nuevo', component: EventoNuevoComponent },
      { path: 'modificar/:id', component: EventoEdicionComponent}
    ] 
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EventoRoutingModule { }
