import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-evento-shell',
  templateUrl: './evento-shell.component.html',
  styleUrls: ['./evento-shell.component.css']
})
export class EventoShellComponent implements OnInit {

  public isCollapsed: boolean = false;

  constructor() { }

  ngOnInit() {
  }

  options = [
    {
      icon: 'file-search',
      title: 'Lista',
      url: 'lista'
    },
    {
      icon: 'file-add',
      title: 'Nuevo Evento',
      url: 'nuevo'
    },
  ];

  public handleCollapse() {
    window.innerWidth < 768 ? this.isCollapsed = true : null;
  }

  public collapseChange() {
    window.innerWidth < 768 ? this.isCollapsed = false : null;
  }

}
